@import url(https://fonts.googleapis.com/css2?family=Gideon+Roman&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Gideon+Roman&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.nav-link {
  text-decoration: none;
  color: snow;
  background-color: rgb(20, 20, 20);
}

.active {
  background-color: rgb(29, 29, 29);
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
}
.nav {
  background-color: rgb(20, 20, 20);
}

.profCont {
  display: flex;
  flex-direction: row;
  padding: 50px 10px;
  font-family: 'Gideon Roman', cursive;
}

.abtMeHeader {
  color: snow;
  text-align: center;
}

.abtMe {
  color: snow;
  padding: 10px 40px;
}

.profPic {
  height: 300px;
  width: 300px;
}

.picCont {
  display: flex;
  justify-content: center;
}

.abtMe p {
  padding-top: 10px;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  /* background-color: rgb(29, 29, 29); */
}

html {
  background-color: rgb(29, 29, 29);
}

body {
  height: 100vh;
  overflow-x: hidden;
  background-color: rgb(29, 29, 29);

}

.projCont {
  background-color: rgb(29, 29, 29);
  position: absolute;
  justify-content: center;
  align-items: center;
  padding-top: 50px;
  width: 100vw;
  display: grid;
  grid-template-columns: auto auto auto;
  grid-column-gap: 10px;
  column-gap: 10px;
  grid-row-gap: 10px;
  row-gap: 10px;
}

.projBox {
  width: 300px;
  height: 150px;
  object-fit: fill;
  object-position: center;
}

.cont {
  display: flex;
  flex-direction: row;
  columns: 2;
  justify-content: space-evenly;
  padding-top: 75px;
}

.iconCont {
  display: flex;
  flex-direction: row;
  align-items: center;
}

.iconBox {
  width: 75px;
  height: 75px;
  margin: 20px;
}

.formHead {
  color: snow;
}

.contForm {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 35vw;
}

.sendBtn {
  width: 100px;
  align-self: center;
}

.contForm textarea {
  padding: 2px 5px;
}

.resCont {
  color: snow;
}

.listCont {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  padding-top: 50px;
}

iframe {
  width: 750px;
  height: 600px;
  overflow: scroll;
}

.animBack {
  width: 100%;
  height: 90px;
  color: #fff;
  background: linear-gradient(-45deg, #ee7752, #e73c7e, #23a6d5, #23d5ab);
  background-size: 400% 400%;
  position: relative;
  animation: change 20s ease infinite;
}

.smallTxt {
  font-size: 65%;
}

h1 {
  text-align: center;
  line-height: 100%;
  padding-top: 20px;
  font-family: 'Gideon Roman', cursive;
  font-weight: 800;
}

@keyframes change {
  0% {
    background-position: 0 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0 50%;
  }
}

body {
  background-color: rgb(37, 37, 37);
}

